import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Link } from 'react-router-dom'

const Container = styled.div`
    position:absolute;
    width:100%;
    height:${window.innerHeight / 2}vh;
    background-color:rgba(215,215,215,1);
    overflow: hidden;
    color:white;
    text-align: center;
`;

const Text = styled.div`
    width:100%;
    height: 200px;
    font-size: 50px;
    padding:50px;
`;

const Top = styled.div`
    position:fixed;
    width:100%;
    height: 200px;
    top:0px;
    background-color:rgba(44, 130, 201, 0.4);
    z-index:2;
    border-bottom:1px solid black;
    transition-property: top;
    transition-duration: 1s;
`;

const Left = styled.div`
    position:fixed;
    width:20%;
    height:calc(100% - 200px);
    top:200px;
    left:0%;
    background-color:rgba(44, 130, 201, 0.4);
    z-index:2;
    transition-property: left;
    transition-duration: 1s;
`;
const Right = styled.div`
    position:fixed;
    width:20%;
    height:calc(100% - 200px);
    top:200px;
    right: 0%;
    background-color:rgba(44, 130, 201, 0.4);
    z-index:2;
    transition-property: right;
    transition-duration: 1s;
`;
//door 이동은 right left 와 함께 이동하며 따라왔다가 이동 완료시 그때 남은 30%를 이동시킨다  scale사용해서 파츠 맞추는 형식도 가능해보임
const Rightdoor = styled.div`
    position:fixed;
    width:calc(30% + 1px);
    height:calc(100% - 200px);
    top:200px;
    right:20%;
    background-color: blueviolet;
    z-index: 1;
    transition-property: right;
    transition-duration: 2s;
`;

const Leftdoor = styled.div`
    position:fixed;
    width:calc(30%);
    height:calc(100% - 200px);
    top:200px;
    left:20%;
    background-color: blueviolet;
    z-index: 1;
    transition-property: left,width;
    transition-duration: 2s;
`;

const Button = styled.div`
    position:fixed;
    right:50px;
    width: 100px;
    height:200px ;
    top:calc(50% - 100px);
    border:1px solid black;
    z-index:999;
    display: none;
`;

const BTN = styled.div`
    position: absolute;
    width: 100%;
    height: 100%;
    top:0;
    z-index: 999;
    &:hover{
        ${Top}{
            
            top:-200px;
        }
        ${Left}{

            left:-20%;
        }
        ${Right}{
            
            right:-20%;
        }
        ${Leftdoor}{
            
            left:calc(-30%);
            width:calc(30)
        }
        ${Rightdoor}{
            
            right:-30%;
        }
    }
`;

const Ul = styled.ul`
    position: absolute;
    display:flex;
    width: 100%;
    top:0;
`;

const Li = styled.li`
    width:100px;
    padding:10px;
    text-align:center;
    margin:20px;
`;

const Body = styled.div`
    width:100%;
    height:100%;
    display:flex;
`;

const Section1 = styled.div`
    width:60%;
    height:100%;
`;

const Section2 = styled.div`
    width:20%;
    height:100%;
`;

const Paper = styled.div`
    width:100%;
    height: calc(100% / 6);
    border:1px solid red;
    display: flex;
    flex-wrap: wrap;
`;

const Paper_1 = styled.div`
    width:30%;
    height:65%;
    background-color: rgba(00,0,0,0.3);
`;

const Paper_2 = styled.div`
    width:70%;
    height:65%;
    background-color: rgba(0,0,0,0.4);
    display: flex;
    flex-wrap:wrap;
`;

const Paper_3 = styled.div`
    width:100%;
    height:35%;
    background-color: rgba(0,0,0,0.5);
    display: flex;
    flex-wrap:wrap;
`;

const Paper_1_1 = styled.div`
    width:100%;
    height: 60%;
    background-image: url(${require('../../assets/React-logo.png').default});
    background-size:cover;
`;

const Paper_2_1_1 = styled.div`
    width:100%;
    height:100%;
    background-image:url(${props => props.BackImg});
    background-position: center;
    background-size: ${props => props.BackSize};
    background-repeat: no-repeat;
    position: relative;
    left: -100%;
    opacity: 0;
    font-size:40px;
    color:rgba(0,0,0,0.8);
    font-weight: 900;
`;

const Paper_2_1 = styled.div`
    width: 33.333%;
    height:50%;
    overflow: hidden;
    &:hover{
        ${Paper_2_1_1}{
        transition-property: all;
        transition-duration: 0.7s;
        opacity: 1;
        left: 0%;
        }
    }
`;

const Span = styled.span`
    position: relative;
    top: 25px;
    color:black;
`;

const Stick = styled.div`
    width:80%;
    height:40px;
    border:2px solid black;
    border-radius: 5px;
    position: relative;
    top:50px;
    margin:auto;
`;

const Result = styled.div`
    width:${props => props.Width};
    height:100%;
    background-color:#3fce02;
    border-radius: 2px;
`;

const Result_txt = styled.span`
    position: absolute;
    text-align:center;
    color:black;
    text-indent:-10px;
    transform: translateY(7px);
    font-size:20px;
`;

const Intro = styled.div`
    width:100%;;
    height:calc(100% / 4);
    background-color: blue;
`;

export default class extends React.Component {
    render() {
        return <Container>
            <Body>
                <Ul>
                    <Li>
                        <Link to="/">Sekchon</Link>
                    </Li>
                    <Li>
                        <Link to="/">Mora</Link>
                    </Li>
                    <Li>
                        <Link to="/react-flix">React-flix</Link>
                    </Li>
                </Ul>
                <Section2>좌</Section2>
                <Section1>
                    <Text>Title</Text>
                    <Paper>
                        <Paper_1>
                            <Paper_1_1 />
                            <div>프로필 사진 및 간단 자기소개</div>
                        </Paper_1>
                        <Paper_2>
                            <Paper_2_1>
                                <Paper_2_1_1
                                    BackImg={require('../../assets/HTML5-logo.png').default}
                                    BackSize={'100% 90%'}
                                />
                            </Paper_2_1>
                            <Paper_2_1>
                                <Paper_2_1_1 BackImg={require('../../assets/CSS3-logo.png').default}
                                    BackSize={'70% 90%'}
                                />
                            </Paper_2_1>
                            <Paper_2_1>
                                <Paper_2_1_1 BackImg={require('../../assets/js-logo.png').default}
                                    BackSize={'100% 90%'}
                                />
                            </Paper_2_1>
                            <Paper_2_1>
                                <Paper_2_1_1 BackImg={require('../../assets/jQuery-logo.png').default}
                                    BackSize={'100% 100%'}
                                />
                            </Paper_2_1>
                            <Paper_2_1>
                                <Paper_2_1_1 BackImg={require('../../assets/PHP-logo.png').default}
                                    BackSize={'100% 80%'}
                                />
                            </Paper_2_1>
                            <Paper_2_1>
                                <Paper_2_1_1
                                    BackImg={require('../../assets/React-logo.png').default}
                                    BackSize={'90% 85%'}
                                >
                                    React
                                </Paper_2_1_1>
                            </Paper_2_1>
                        </Paper_2>
                        <Paper_3>
                            <Paper_2_1>
                                <Span> HTML5 </Span>
                                <Stick>
                                    <Result_txt>95%</Result_txt>
                                    <Result
                                        Width={"95%"} />
                                </Stick>
                            </Paper_2_1>
                            <Paper_2_1>
                                <Span> CSS3 </Span>
                                <Stick>
                                    <Result_txt>90%</Result_txt>
                                    <Result
                                        Width={"90%"} />
                                </Stick>
                            </Paper_2_1>
                            <Paper_2_1>
                                <Span> JavaScript </Span>
                                <Stick>
                                    <Result_txt>40%</Result_txt>
                                    <Result
                                        Width={"40%"} />
                                </Stick>
                            </Paper_2_1>
                            <Paper_2_1>
                                <Span> jQuery </Span>
                                <Stick>
                                    <Result_txt>80%</Result_txt>
                                    <Result
                                        Width={"80%"} />
                                </Stick>
                            </Paper_2_1>
                            <Paper_2_1>
                                <Span> PHP </Span>
                                <Stick>
                                    <Result_txt>30%</Result_txt>
                                    <Result
                                        Width={"30%"} />
                                </Stick>
                            </Paper_2_1>
                            <Paper_2_1>
                                <Span> React </Span>
                                <Stick>
                                    <Result_txt>30%</Result_txt>
                                    <Result
                                        Width={"30%"} />
                                </Stick>
                            </Paper_2_1>
                        </Paper_3>
                    </Paper>
                    <Intro>
                        좌 중[main] 우 형태의 슬라이드와 슬라이드 넘김에 따라 변경되는 txt이벤트 설정
                    </Intro>
                </Section1>
                <Section2>우</Section2>
            </Body>
            <BTN>
                <Top Top={Top + 'px'}>1F</Top>
                <Left>좌측</Left>
                <Leftdoor>Leftdoor</Leftdoor>
                <Right>버튼, 1F</Right>
                <Rightdoor>Rightdoor</Rightdoor>
            </BTN>
            <Button>버튼영역</Button>
        </Container>
    }
}